import { useEffect } from 'react';
import { Container, ListItem, List, Card, CardContent, Typography, ListItemText, Button, LinearProgress } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import Page from 'src/components/Page';
import { useDispatch, useSelector } from 'src/store';
import { retrieveBlock } from 'src/store/slices/slate/block';
import { useParams, Link } from 'react-router-dom';

const BlockDetails = () => {
  const dispatch = useDispatch();

  const { id } = useParams();

  const { currentBlock, loading } = useSelector((state) => state.slate.block);

  useEffect(() => {
    dispatch(retrieveBlock(id));
  }, []);

  return (
    <Page title="Block Details - Block Explorer">
      <Container maxWidth="lg" sx={{ pt: 2 }}>
        <Button component={Link} to="/block-explorer/transactions" startIcon={<ArrowBackIcon />}>
          Blocks
        </Button>
        {loading && <LinearProgress />}
        <Card sx={{ mb: 2 }}>
          <List dense={false}>
            <ListItem secondaryAction={currentBlock?.hash}>
              <ListItemText primary="Hash" />
            </ListItem>
            <ListItem secondaryAction={currentBlock?.height}>
              <ListItemText primary="Block" />
            </ListItem>
            <ListItem secondaryAction={currentBlock?.transaction_ids?.length ?? 0}>
              <ListItemText primary="Number of transactions" />
            </ListItem>
            <ListItem secondaryAction={currentBlock?.time}>
              <ListItemText primary="Time" />
            </ListItem>
            <ListItem secondaryAction={currentBlock?.miner}>
              <ListItemText primary="Miner" />
            </ListItem>
            <ListItem secondaryAction={currentBlock?.reward}>
              <ListItemText primary="Block reward" />
            </ListItem>
          </List>
        </Card>
        <Card sx={{ my: 2 }}>
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              Latest transactions in Block
            </Typography>
            <List dense={false}>
              {currentBlock?.transaction_ids?.map(item => <ListItem>
                <ListItemText key={item} primary={<Link to={`/block-explorer/transactions/${item}`}>${item}</Link>} />
              </ListItem>)}
            </List>
          </CardContent>
        </Card>
      </Container>
    </Page>
  );
};

export default BlockDetails;
